.Camera{
  position: fixed;
  top: 0;
  left: 0;

  width: 100vw;
  height: 100vh;
  background: black;
}

.Camera video{
  z-index: 10;
  position: fixed;
  top: 0;
  left: 0;

  width: 100vw;
  height: 100vh;
}

.Camera .DrawCanvas{
  z-index: 20;
  position: fixed;
  top: 0;
  left: 0;

  width: 100vw;
  height: 100vh;
}

.Camera .Overlay{
  z-index: 30;
  position: fixed;
  top: 0;
  left: 0;

  width: 100vw;
  height: 100vh;

  display: -ms-flexbox;

  display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-wrap: nowrap;
	    flex-wrap: nowrap;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-line-pack: stretch;
	    align-content: stretch;
}

.Camera .Overlay img{
  
  height: 80%;
}


.Camera .snap{
  position: fixed;
  bottom: calc( 30px + 10px);
  left: calc( 50vw - 50px - 10px);

  width: 100px;
  height: 100px;
  border-radius: 1000px;

  background: #eeeeee;
  border: 20px solid rgba(30,30,30,50%);

  -webkit-transition: 100ms all;

  -o-transition: 100ms all;

  transition: 100ms all;


}

.Camera:active .snap{
  border: 20px solid rgba(30,30,30,20%);

}


